import React, { createContext, useContext, useState, useEffect, ReactNode } from 'react';
import { supabase } from '../lib/supabaseClient';
import { UserProfile } from '../types/database';
import { getStorageData, setStorageData, clearStorageData } from '../utils/storage';
import { isOnline, addOfflineAction, updateLastSync } from '../utils/offline';
import { toast } from 'sonner';

interface AuthContextType {
  user: UserProfile | null;
  loading: boolean;
  login: (username: string, password: string) => Promise<void>;
  register: (userData: RegisterData) => Promise<void>;
  logout: () => Promise<void>;
  updateProfile: (profileData: Partial<UserProfile['profile']>) => Promise<void>;
  updateCourse: (course: string) => Promise<void>;
  syncWithSupabase: () => Promise<void>;
  activeUsersCount: number;
}

interface RegisterData {
  username: string;
  password: string;
  email: string;
  role: 'student' | 'teacher' | 'admin';
  course: string;
  firstName?: string;
  lastName?: string;
  phone?: string;
}

const AuthContext = createContext<AuthContextType | undefined>(undefined);

export const useAuth = () => {
  const context = useContext(AuthContext);
  if (!context) {
    throw new Error('useAuth must be used within AuthProvider');
  }
  return context;
};

export const AuthProvider: React.FC<{ children: ReactNode }> = ({ children }) => {
  const [user, setUser] = useState<UserProfile | null>(null);
  const [loading, setLoading] = useState(true);
  const [activeUsersCount, setActiveUsersCount] = useState(0);

  // :   Supabase
  useEffect(() => {
    const initAuth = async () => {
      try {
        //    Supabase
        const { data: { session } } = await supabase.auth.getSession();
        
        if (session?.user) {
          //    Supabase
          await loadUserProfile(session.user.id);
        } else {
          //    Supabase,  localStorage
          clearStorageData();
        }
      } catch (error) {
        console.error('Error initializing auth:', error);
      } finally {
        setLoading(false);
      }
    };

    initAuth();

    //    auth state
    const { data: { subscription } } = supabase.auth.onAuthStateChange(async (event, session) => {
      if (event === 'SIGNED_IN' && session?.user) {
        await loadUserProfile(session.user.id);
      } else if (event === 'SIGNED_OUT') {
        setUser(null);
        clearStorageData();
      }
    });

    return () => {
      subscription.unsubscribe();
    };
  }, []);

  //   
  useEffect(() => {
    const updateActiveUsers = async () => {
      try {
        const { count } = await supabase
          .from('users_profiles')
          .select('*', { count: 'exact', head: true });
        setActiveUsersCount(count || 0);
      } catch (error) {
        console.error('Error counting users:', error);
      }
    };

    updateActiveUsers();
    const interval = setInterval(updateActiveUsers, 30000); //   30 

    return () => clearInterval(interval);
  }, [user]);

  const loadUserProfile = async (userId: string) => {
    try {
      const { data: profile, error } = await supabase
        .from('users_profiles')
        .select('*')
        .eq('id', userId)
        .single();

      if (error) throw error;

      if (profile) {
        const userProfile: UserProfile = {
          id: profile.id,
          username: profile.username,
          role: profile.role,
          course: profile.course,
          registrationDate: profile.registration_date,
          lastLogin: profile.last_login || undefined,
          supabase_id: userId,
          profile: {
            firstName: profile.first_name || undefined,
            lastName: profile.last_name || undefined,
            phone: profile.phone || undefined,
            avatar_url: profile.avatar_url || undefined,
          }
        };

        setUser(userProfile);
        //      localStorage ( !)
        setStorageData({ currentUser: userProfile });

        //  last_login
        await supabase
          .from('users_profiles')
          .update({ last_login: new Date().toISOString() })
          .eq('id', userId);
      }
    } catch (error) {
      console.error('Error loading user profile:', error);
      toast.error('   ');
    }
  };

  const login = async (usernameOrEmail: string, password: string) => {
    try {
      // ,    
      if (!password || password.trim() === '') {
        throw new Error('    ');
      }

      // ,  email  username
      const isEmail = usernameOrEmail.includes('@');
      
      let email = usernameOrEmail;
      
      //   username,   email
      if (!isEmail) {
        const { data: profile, error: profileError } = await supabase
          .from('users_profiles')
          .select('id')
          .eq('username', usernameOrEmail)
          .single();

        if (profileError || !profile) {
          throw new Error('  . , .');
        }

        //  email  auth.users
        const { data: { user: authUser }, error: authError } = await supabase.auth.admin.getUserById(profile.id);
        
        if (authError || !authUser?.email) {
          throw new Error('   email ');
        }
        
        email = authUser.email;
      }

      //   Supabase Auth (    )
      const { data, error } = await supabase.auth.signInWithPassword({
        email: email,
        password: password,
      });

      if (error) {
        if (error.message.includes('Invalid login credentials')) {
          throw new Error('    ');
        }
        throw error;
      }

      if (data.user) {
        await loadUserProfile(data.user.id);
        toast.success('  !');
      }
    } catch (error: any) {
      console.error('Login error:', error);
      toast.error(error.message || ' ');
      throw error;
    }
  };

  const register = async (userData: RegisterData) => {
    try {
      //  
      if (!userData.password || userData.password.length < 6) {
        throw new Error('    6 ');
      }

      // ,        username
      const { data: existingProfile, error: checkError } = await supabase
        .from('users_profiles')
        .select('username')
        .eq('username', userData.username)
        .single();

      if (existingProfile) {
        throw new Error('     ');
      }

      //   Supabase Auth (  )
      const { data: authData, error: authError } = await supabase.auth.signUp({
        email: userData.email,
        password: userData.password,
        options: {
          data: { 
            username: userData.username,
            role: userData.role 
          }
        }
      });

      if (authError) {
        if (authError.message.includes('already registered')) {
          throw new Error('   email  ');
        }
        throw authError;
      }

      if (!authData.user) {
        throw new Error('   ');
      }

      //    users_profiles
      const { error: profileError } = await supabase
        .from('users_profiles')
        .insert({
          id: authData.user.id,
          username: userData.username,
          role: userData.role,
          course: userData.course,
          first_name: userData.firstName,
          last_name: userData.lastName,
          phone: userData.phone,
          registration_date: new Date().toISOString()
        });

      if (profileError) {
        //     ,    auth
        await supabase.auth.admin.deleteUser(authData.user.id);
        throw profileError;
      }

      //    
      await loadUserProfile(authData.user.id);
      
      toast.success('  !');
    } catch (error: any) {
      console.error('Registration error:', error);
      toast.error(error.message || ' ');
      throw error;
    }
  };

  const logout = async () => {
    try {
      await supabase.auth.signOut();
      setUser(null);
      clearStorageData();
      toast.success('  ');
    } catch (error: any) {
      console.error('Logout error:', error);
      toast.error('  ');
    }
  };

  const updateProfile = async (profileData: Partial<UserProfile['profile']>) => {
    if (!user) return;

    try {
      if (isOnline()) {
        const { error } = await supabase
          .from('users_profiles')
          .update({
            first_name: profileData.firstName,
            last_name: profileData.lastName,
            phone: profileData.phone,
            avatar_url: profileData.avatar_url,
            updated_at: new Date().toISOString()
          })
          .eq('id', user.id);

        if (error) throw error;
        updateLastSync();
      } else {
        addOfflineAction({
          type: 'update',
          table: 'users_profiles',
          data: { id: user.id, ...profileData }
        });
      }

      const updatedUser = {
        ...user,
        profile: { ...user.profile, ...profileData }
      };
      setUser(updatedUser);
      setStorageData({ currentUser: updatedUser });

      toast.success(' ');
    } catch (error: any) {
      console.error('Profile update error:', error);
      toast.error('  ');
    }
  };

  const updateCourse = async (course: string) => {
    if (!user) return;

    try {
      if (isOnline()) {
        const { error } = await supabase
          .from('users_profiles')
          .update({
            course,
            updated_at: new Date().toISOString()
          })
          .eq('id', user.id);

        if (error) throw error;
        updateLastSync();
      } else {
        addOfflineAction({
          type: 'update',
          table: 'users_profiles',
          data: { id: user.id, course }
        });
      }

      const updatedUser = { ...user, course };
      setUser(updatedUser);
      setStorageData({ currentUser: updatedUser });

      toast.success(' ');
    } catch (error: any) {
      console.error('Course update error:', error);
      toast.error('  ');
    }
  };

  const syncWithSupabase = async () => {
    if (!isOnline() || !user) return;

    try {
      await loadUserProfile(user.id);
      updateLastSync();
      toast.success(' ');
    } catch (error) {
      console.error('Sync error:', error);
      toast.error(' ');
    }
  };

  const value: AuthContextType = {
    user,
    loading,
    login,
    register,
    logout,
    updateProfile,
    updateCourse,
    syncWithSupabase,
    activeUsersCount
  };

  return <AuthContext.Provider value={value}>{children}</AuthContext.Provider>;
};
