import { useState, useEffect } from "react";
import { Card, CardContent, CardHeader, CardTitle } from "./ui/card";
import { Button } from "./ui/button";
import { Input } from "./ui/input";
import { Label } from "./ui/label";
import { Textarea } from "./ui/textarea";
import { Switch } from "./ui/switch";
import { Alert, AlertDescription } from "./ui/alert";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "./ui/tabs";
import { Separator } from "./ui/separator";
import { 
  Settings, Database, Palette, Bell, 
  Shield, Globe, Monitor, Download, 
  Upload, RotateCcw, Trash2 
} from "lucide-react";
import { toast } from "../lib/toast";

interface SystemSettings {
  siteName: string;
  siteDescription: string;
  maintenanceMode: boolean;
  registrationEnabled: boolean;
  defaultCourse: string;
  maxFileSize: number;
  allowedFileTypes: string[];
  notifications: {
    achievements: boolean;
    announcements: boolean;
    reminders: boolean;
  };
  theme: {
    defaultTheme: 'light' | 'dark' | 'system';
    allowThemeSwitch: boolean;
  };
  backup: {
    autoBackup: boolean;
    backupInterval: number; //  
  };
}

export function AdminSystemSettings() {
  const [settings, setSettings] = useState<SystemSettings>({
    siteName: " ",
    siteDescription: "    ",
    maintenanceMode: false,
    registrationEnabled: true,
    defaultCourse: "1",
    maxFileSize: 10, // MB
    allowedFileTypes: ["pdf", "doc", "docx", "txt", "md"],
    notifications: {
      achievements: true,
      announcements: true,
      reminders: true
    },
    theme: {
      defaultTheme: 'system',
      allowThemeSwitch: true
    },
    backup: {
      autoBackup: false,
      backupInterval: 7
    }
  });

  const [customCSS, setCustomCSS] = useState("");
  const [customJS, setCustomJS] = useState("");

  //    
  useEffect(() => {
    loadSettings();
  }, []);

  const loadSettings = () => {
    const savedSettings = localStorage.getItem('system_settings');
    if (savedSettings) {
      setSettings(JSON.parse(savedSettings));
    }

    const savedCSS = localStorage.getItem('custom_css');
    if (savedCSS) {
      setCustomCSS(savedCSS);
    }

    const savedJS = localStorage.getItem('custom_js');
    if (savedJS) {
      setCustomJS(savedJS);
    }
  };

  const saveSettings = () => {
    localStorage.setItem('system_settings', JSON.stringify(settings));
    toast.success(" ");
  };

  const saveCustomCSS = () => {
    localStorage.setItem('custom_css', customCSS);
    
    //  CSS  
    let existingStyle = document.getElementById('custom-admin-css');
    if (existingStyle) {
      existingStyle.remove();
    }

    if (customCSS.trim()) {
      const style = document.createElement('style');
      style.id = 'custom-admin-css';
      style.textContent = customCSS;
      document.head.appendChild(style);
    }

    toast.success("  ");
  };

  const saveCustomJS = () => {
    localStorage.setItem('custom_js', customJS);
    
    if (customJS.trim()) {
      try {
        //   JavaScript
        new Function(customJS)();
        toast.success(" JavaScript ");
      } catch (error) {
        toast.error("  JavaScript ");
        console.error(error);
      }
    }
  };

  //    
  const createFullBackup = () => {
    const backup = {
      timestamp: new Date().toISOString(),
      users: localStorage.getItem('users'),
      system_settings: localStorage.getItem('system_settings'),
      achievements: localStorage.getItem('achievements'),
      custom_css: localStorage.getItem('custom_css'),
      custom_js: localStorage.getItem('custom_js'),
      courses: localStorage.getItem('courses'),
      files: localStorage.getItem('files'),
      //    
      custom_sections: {}
    };

    //   
    for (let i = 0; i < localStorage.length; i++) {
      const key = localStorage.key(i);
      if (key && key.startsWith('admin_section_')) {
        (backup.custom_sections as any)[key] = localStorage.getItem(key);
      }
    }

    const blob = new Blob([JSON.stringify(backup, null, 2)], { type: 'application/json' });
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = `full-backup-${new Date().toISOString().slice(0, 10)}.json`;
    a.click();
    URL.revokeObjectURL(url);
    
    toast.success("   ");
  };

  //    
  const restoreFromBackup = (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (!file) return;

    const reader = new FileReader();
    reader.onload = (e) => {
      try {
        const backup = JSON.parse(e.target?.result as string);
        
        if (confirm("     . ?")) {
          //   
          if (backup.users) localStorage.setItem('users', backup.users);
          if (backup.system_settings) localStorage.setItem('system_settings', backup.system_settings);
          if (backup.achievements) localStorage.setItem('achievements', backup.achievements);
          if (backup.custom_css) localStorage.setItem('custom_css', backup.custom_css);
          if (backup.custom_js) localStorage.setItem('custom_js', backup.custom_js);
          if (backup.courses) localStorage.setItem('courses', backup.courses);
          if (backup.files) localStorage.setItem('files', backup.files);

          //   
          if (backup.custom_sections) {
            Object.entries(backup.custom_sections).forEach(([key, value]) => {
              if (typeof value === 'string') {
                localStorage.setItem(key, value);
              }
            });
          }

          toast.success(" .   .");
          setTimeout(() => window.location.reload(), 2000);
        }
      } catch (error) {
        toast.error("   ");
      }
    };
    reader.readAsText(file);
  };

  //   
  const clearAllData = () => {
    if (confirm("!      . ?")) {
      if (confirm(" !  ,     .")) {
        //     
        const currentUser = localStorage.getItem('currentUser');
        const adminKey = 'Starkver_123';
        const adminData = {
          role: 'admin',
          username: 'Starkver',
          created: new Date().toISOString()
        };

        localStorage.clear();
        
        //  
        localStorage.setItem('users', JSON.stringify({ [adminKey]: adminData }));
        if (currentUser) {
          localStorage.setItem('currentUser', currentUser);
        }

        toast.success("  .   .");
        setTimeout(() => window.location.reload(), 2000);
      }
    }
  };

  //     localStorage
  const getStorageSize = () => {
    let total = 0;
    for (let key in localStorage) {
      if (localStorage.hasOwnProperty(key)) {
        total += localStorage[key].length;
      }
    }
    return (total / 1024).toFixed(2); // KB
  };

  const updateSetting = (path: string, value: any) => {
    const keys = path.split('.');
    const newSettings = { ...settings };
    let current: any = newSettings;
    
    for (let i = 0; i < keys.length - 1; i++) {
      current = current[keys[i]];
    }
    current[keys[keys.length - 1]] = value;
    
    setSettings(newSettings);
  };

  return (
    <div className="space-y-6">
      <div className="text-center py-4">
        <h2 className="text-2xl mb-2"> </h2>
        <p className="text-muted-foreground">
             
        </p>
      </div>

      <Tabs defaultValue="general" className="w-full">
        <TabsList className="grid w-full grid-cols-5">
          <TabsTrigger value="general"></TabsTrigger>
          <TabsTrigger value="features"></TabsTrigger>
          <TabsTrigger value="theme"> </TabsTrigger>
          <TabsTrigger value="backup"> </TabsTrigger>
          <TabsTrigger value="advanced"></TabsTrigger>
        </TabsList>

        <TabsContent value="general" className="space-y-6">
          <Card>
            <CardHeader>
              <CardTitle className="flex items-center gap-2">
                <Globe className="h-5 w-5" />
                 
              </CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              <div>
                <Label> :</Label>
                <Input
                  value={settings.siteName}
                  onChange={(e) => updateSetting('siteName', e.target.value)}
                  placeholder=" "
                />
              </div>
              
              <div>
                <Label> :</Label>
                <Textarea
                  value={settings.siteDescription}
                  onChange={(e) => updateSetting('siteDescription', e.target.value)}
                  placeholder="   "
                  rows={3}
                />
              </div>

              <div>
                <Label>     :</Label>
                <Input
                  value={settings.defaultCourse}
                  onChange={(e) => updateSetting('defaultCourse', e.target.value)}
                  placeholder="1"
                />
              </div>

              <Button onClick={saveSettings}>
                <Settings className="h-4 w-4 mr-2" />
                  
              </Button>
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="features" className="space-y-6">
          <Card>
            <CardHeader>
              <CardTitle className="flex items-center gap-2">
                <Shield className="h-5 w-5" />
                 
              </CardTitle>
            </CardHeader>
            <CardContent className="space-y-6">
              <div className="flex items-center justify-between">
                <div>
                  <Label> </Label>
                  <p className="text-sm text-muted-foreground">
                           
                  </p>
                </div>
                <Switch
                  checked={settings.maintenanceMode}
                  onCheckedChange={(checked) => updateSetting('maintenanceMode', checked)}
                />
              </div>

              <Separator />

              <div className="flex items-center justify-between">
                <div>
                  <Label>  </Label>
                  <p className="text-sm text-muted-foreground">
                      
                  </p>
                </div>
                <Switch
                  checked={settings.registrationEnabled}
                  onCheckedChange={(checked) => updateSetting('registrationEnabled', checked)}
                />
              </div>

              <Separator />

              <div>
                <Label>   (MB):</Label>
                <Input
                  type="number"
                  value={settings.maxFileSize}
                  onChange={(e) => updateSetting('maxFileSize', parseInt(e.target.value))}
                  min="1"
                  max="100"
                />
              </div>

              <div>
                <Label>   ( ):</Label>
                <Input
                  value={settings.allowedFileTypes.join(', ')}
                  onChange={(e) => updateSetting('allowedFileTypes', e.target.value.split(',').map(s => s.trim()))}
                  placeholder="pdf, doc, docx, txt, md"
                />
              </div>

              <Button onClick={saveSettings}>
                <Settings className="h-4 w-4 mr-2" />
                  
              </Button>
            </CardContent>
          </Card>

          <Card>
            <CardHeader>
              <CardTitle className="flex items-center gap-2">
                <Bell className="h-5 w-5" />
                
              </CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              <div className="flex items-center justify-between">
                <Label>  </Label>
                <Switch
                  checked={settings.notifications.achievements}
                  onCheckedChange={(checked) => updateSetting('notifications.achievements', checked)}
                />
              </div>

              <div className="flex items-center justify-between">
                <Label>  </Label>
                <Switch
                  checked={settings.notifications.announcements}
                  onCheckedChange={(checked) => updateSetting('notifications.announcements', checked)}
                />
              </div>

              <div className="flex items-center justify-between">
                <Label></Label>
                <Switch
                  checked={settings.notifications.reminders}
                  onCheckedChange={(checked) => updateSetting('notifications.reminders', checked)}
                />
              </div>
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="theme" className="space-y-6">
          <Card>
            <CardHeader>
              <CardTitle className="flex items-center gap-2">
                <Palette className="h-5 w-5" />
                 
              </CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              <div>
                <Label> CSS:</Label>
                <Textarea
                  value={customCSS}
                  onChange={(e) => setCustomCSS(e.target.value)}
                  rows={10}
                  className="font-mono text-sm"
                  placeholder="/*    */
.custom-button {
  background: linear-gradient(45deg, #ff6b6b, #4ecdc4);
  border: none;
  color: white;
}"
                />
              </div>
              
              <Button onClick={saveCustomCSS}>
                <Palette className="h-4 w-4 mr-2" />
                 
              </Button>
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="backup" className="space-y-6">
          <Card>
            <CardHeader>
              <CardTitle className="flex items-center gap-2">
                <Database className="h-5 w-5" />
                 
              </CardTitle>
            </CardHeader>
            <CardContent className="space-y-6">
              <Alert>
                <Database className="h-4 w-4" />
                <AlertDescription>
                     localStorage: {getStorageSize()} KB
                </AlertDescription>
              </Alert>

              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <Button onClick={createFullBackup} className="w-full">
                  <Download className="h-4 w-4 mr-2" />
                     
                </Button>

                <div>
                  <input
                    type="file"
                    accept=".json"
                    onChange={restoreFromBackup}
                    className="hidden"
                    id="restore-backup"
                  />
                  <Button
                    onClick={() => document.getElementById('restore-backup')?.click()}
                    variant="outline"
                    className="w-full"
                  >
                    <Upload className="h-4 w-4 mr-2" />
                      
                  </Button>
                </div>
              </div>

              <Separator />

              <div>
                <Label className="text-destructive"> </Label>
                <p className="text-sm text-muted-foreground mb-4">
                      
                </p>
                
                <Button
                  onClick={clearAllData}
                  variant="destructive"
                  className="w-full"
                >
                  <Trash2 className="h-4 w-4 mr-2" />
                     
                </Button>
              </div>
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="advanced" className="space-y-6">
          <Card>
            <CardHeader>
              <CardTitle className="flex items-center gap-2">
                <Monitor className="h-5 w-5" />
                 JavaScript
              </CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              <Alert>
                <Shield className="h-4 w-4" />
                <AlertDescription>
                  !  JavaScript    .
                </AlertDescription>
              </Alert>

              <div>
                <Label>JavaScript :</Label>
                <Textarea
                  value={customJS}
                  onChange={(e) => setCustomJS(e.target.value)}
                  rows={10}
                  className="font-mono text-sm"
                  placeholder="//   JavaScript
console.log('  ');

// ,   
document.addEventListener('DOMContentLoaded', function() {
  //   
});"
                />
              </div>
              
              <Button onClick={saveCustomJS} variant="outline">
                <Monitor className="h-4 w-4 mr-2" />
                 JavaScript
              </Button>
            </CardContent>
          </Card>

          <Card>
            <CardHeader>
              <CardTitle>  </CardTitle>
            </CardHeader>
            <CardContent className="space-y-2">
              <div className="text-sm">
                <strong>:</strong> 1.0.0
              </div>
              <div className="text-sm">
                <strong> :</strong> {new Date().toLocaleDateString()}
              </div>
              <div className="text-sm">
                <strong>:</strong> {navigator.userAgent.includes('Chrome') ? 'Chrome' : 
                  navigator.userAgent.includes('Firefox') ? 'Firefox' : ''}
              </div>
              <div className="text-sm">
                <strong> localStorage:</strong> {typeof(Storage) !== "undefined" ? '' : ''}
              </div>
            </CardContent>
          </Card>
        </TabsContent>
      </Tabs>
    </div>
  );
}