import { useState, useEffect } from "react";
import { Card, CardContent, CardHeader, CardTitle } from "./ui/card";
import { Button } from "./ui/button";
import { Input } from "./ui/input";
import { Label } from "./ui/label";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "./ui/select";
import { Alert, AlertDescription } from "./ui/alert";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "./ui/tabs";
import { Badge } from "./ui/badge";
import { 
  Users, UserPlus, UserMinus, Edit, Save, 
  Trash2, Search, Filter, Download, 
  Shield, User, GraduationCap 
} from "lucide-react";
import { toast } from "../lib/toast";

interface UserData {
  username: string;
  role: 'student' | 'teacher' | 'admin';
  course?: string;
  created: string;
  lastLogin?: string;
  email?: string;
}

export function AdminUserManager() {
  const [users, setUsers] = useState<Record<string, UserData>>({});
  const [filteredUsers, setFilteredUsers] = useState<Array<[string, UserData]>>([]);
  const [searchTerm, setSearchTerm] = useState("");
  const [roleFilter, setRoleFilter] = useState("all");
  const [courseFilter, setCourseFilter] = useState("all");
  const [editingUser, setEditingUser] = useState<string | null>(null);
  const [newUser, setNewUser] = useState({
    username: "",
    password: "",
    role: "student" as const,
    course: "",
    email: ""
  });

  //  
  useEffect(() => {
    loadUsers();
  }, []);

  //  
  useEffect(() => {
    let filtered = Object.entries(users);

    if (searchTerm) {
      filtered = filtered.filter(([, user]) => 
        user.username.toLowerCase().includes(searchTerm.toLowerCase()) ||
        user.email?.toLowerCase().includes(searchTerm.toLowerCase())
      );
    }

    if (roleFilter !== "all") {
      filtered = filtered.filter(([, user]) => user.role === roleFilter);
    }

    if (courseFilter !== "all") {
      filtered = filtered.filter(([, user]) => user.course === courseFilter);
    }

    setFilteredUsers(filtered);
  }, [users, searchTerm, roleFilter, courseFilter]);

  const loadUsers = () => {
    const usersData = localStorage.getItem('users');
    if (usersData) {
      const parsedUsers = JSON.parse(usersData);
      const usersList: Record<string, UserData> = {};
      
      //    "username_password"    
      Object.entries(parsedUsers).forEach(([key, userData]: [string, any]) => {
        const [username] = key.split('_');
        if (!usersList[username]) {
          usersList[username] = {
            username,
            role: userData.role,
            course: userData.course,
            created: userData.created || new Date().toISOString(),
            lastLogin: userData.lastLogin,
            email: userData.email
          };
        }
      });
      
      setUsers(usersList);
    }
  };

  //   
  const createUser = () => {
    if (!newUser.username || !newUser.password) {
      toast.error("    ");
      return;
    }

    const usersData = JSON.parse(localStorage.getItem('users') || '{}');
    const userKey = `${newUser.username}_${newUser.password}`;
    
    if (usersData[userKey]) {
      toast.error("     ");
      return;
    }

    const userData = {
      role: newUser.role,
      course: newUser.course,
      email: newUser.email,
      created: new Date().toISOString()
    };

    usersData[userKey] = userData;
    localStorage.setItem('users', JSON.stringify(usersData));
    
    setNewUser({
      username: "",
      password: "",
      role: "student",
      course: "",
      email: ""
    });
    
    loadUsers();
    toast.success(" ");
  };

  //  
  const deleteUser = (username: string) => {
    if (username === 'Starkver') {
      toast.error("   ");
      return;
    }

    if (!confirm(` ,     ${username}?`)) {
      return;
    }

    const usersData = JSON.parse(localStorage.getItem('users') || '{}');
    
    //      (  )
    Object.keys(usersData).forEach(key => {
      if (key.startsWith(`${username}_`)) {
        delete usersData[key];
      }
    });

    localStorage.setItem('users', JSON.stringify(usersData));
    loadUsers();
    toast.success(" ");
  };

  //   
  const changeUserRole = (username: string, newRole: string) => {
    if (username === 'Starkver' && newRole !== 'admin') {
      toast.error("    ");
      return;
    }

    const usersData = JSON.parse(localStorage.getItem('users') || '{}');
    
    //       
    Object.keys(usersData).forEach(key => {
      if (key.startsWith(`${username}_`)) {
        usersData[key].role = newRole;
      }
    });

    localStorage.setItem('users', JSON.stringify(usersData));
    loadUsers();
    toast.success("  ");
  };

  //   
  const resetUserPassword = (username: string, newPassword: string) => {
    if (!newPassword) {
      toast.error("  ");
      return;
    }

    const usersData = JSON.parse(localStorage.getItem('users') || '{}');
    
    //    
    let oldUserData = null;
    let oldKey = null;
    
    Object.entries(usersData).forEach(([key, userData]) => {
      if (key.startsWith(`${username}_`)) {
        oldUserData = userData;
        oldKey = key;
      }
    });

    if (!oldUserData || !oldKey) {
      toast.error("  ");
      return;
    }

    //         
    delete usersData[oldKey];
    const newKey = `${username}_${newPassword}`;
    usersData[newKey] = oldUserData;

    localStorage.setItem('users', JSON.stringify(usersData));
    toast.success(" ");
  };

  //  
  const exportUsers = () => {
    const usersList = Object.entries(users).map(([username, data]) => ({
      username,
      role: data.role,
      course: data.course,
      created: data.created,
      email: data.email
    }));

    const blob = new Blob([JSON.stringify(usersList, null, 2)], { type: 'application/json' });
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = `users-${new Date().toISOString().split('T')[0]}.json`;
    a.click();
    URL.revokeObjectURL(url);
    toast.success("  ");
  };

  const getRoleIcon = (role: string) => {
    switch (role) {
      case 'admin': return Shield;
      case 'teacher': return GraduationCap;
      default: return User;
    }
  };

  const getRoleBadgeVariant = (role: string) => {
    switch (role) {
      case 'admin': return 'destructive';
      case 'teacher': return 'default';
      default: return 'secondary';
    }
  };

  return (
    <div className="space-y-6">
      <div className="text-center py-4">
        <h2 className="text-2xl mb-2"> </h2>
        <p className="text-muted-foreground">
          ,     
        </p>
      </div>

      <Tabs defaultValue="users" className="w-full">
        <TabsList className="grid w-full grid-cols-3">
          <TabsTrigger value="users"> </TabsTrigger>
          <TabsTrigger value="create"> </TabsTrigger>
          <TabsTrigger value="stats"></TabsTrigger>
        </TabsList>

        <TabsContent value="users" className="space-y-6">
          {/*    */}
          <Card>
            <CardHeader>
              <CardTitle className="flex items-center gap-2">
                <Filter className="h-5 w-5" />
                  
              </CardTitle>
            </CardHeader>
            <CardContent>
              <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
                <div>
                  <Label>:</Label>
                  <div className="relative">
                    <Search className="absolute left-3 top-3 h-4 w-4 text-muted-foreground" />
                    <Input
                      value={searchTerm}
                      onChange={(e) => setSearchTerm(e.target.value)}
                      placeholder="  email..."
                      className="pl-10"
                    />
                  </div>
                </div>
                
                <div>
                  <Label>:</Label>
                  <Select value={roleFilter} onValueChange={setRoleFilter}>
                    <SelectTrigger>
                      <SelectValue />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value="all"> </SelectItem>
                      <SelectItem value="student"></SelectItem>
                      <SelectItem value="teacher"></SelectItem>
                      <SelectItem value="admin"></SelectItem>
                    </SelectContent>
                  </Select>
                </div>
                
                <div>
                  <Label>:</Label>
                  <Select value={courseFilter} onValueChange={setCourseFilter}>
                    <SelectTrigger>
                      <SelectValue />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value="all"> </SelectItem>
                      <SelectItem value="1">1 </SelectItem>
                      <SelectItem value="2">2 </SelectItem>
                      <SelectItem value="3">3 </SelectItem>
                      <SelectItem value="4">4 </SelectItem>
                      <SelectItem value="5">5 </SelectItem>
                      <SelectItem value="6">6 </SelectItem>
                    </SelectContent>
                  </Select>
                </div>
                
                <div className="flex items-end">
                  <Button onClick={exportUsers} variant="outline" className="w-full">
                    <Download className="h-4 w-4 mr-2" />
                    
                  </Button>
                </div>
              </div>
            </CardContent>
          </Card>

          {/*   */}
          <Card>
            <CardHeader>
              <CardTitle className="flex items-center gap-2">
                <Users className="h-5 w-5" />
                 ({filteredUsers.length})
              </CardTitle>
            </CardHeader>
            <CardContent>
              <div className="space-y-4">
                {filteredUsers.map(([username, user]) => {
                  const RoleIcon = getRoleIcon(user.role);
                  return (
                    <div
                      key={username}
                      className="flex items-center justify-between p-4 border rounded-lg"
                    >
                      <div className="flex items-center gap-4">
                        <div className="flex items-center gap-2">
                          <RoleIcon className="h-5 w-5" />
                          <div>
                            <div className="font-medium">{username}</div>
                            {user.email && (
                              <div className="text-sm text-muted-foreground">{user.email}</div>
                            )}
                          </div>
                        </div>
                        
                        <div className="flex gap-2">
                          <Badge variant={getRoleBadgeVariant(user.role) as any}>
                            {user.role === 'admin' ? '' : 
                             user.role === 'teacher' ? '' : ''}
                          </Badge>
                          {user.course && (
                            <Badge variant="outline">{user.course} </Badge>
                          )}
                        </div>
                      </div>

                      <div className="flex gap-2">
                        <Select
                          value={user.role}
                          onValueChange={(newRole) => changeUserRole(username, newRole)}
                          disabled={username === 'Starkver'}
                        >
                          <SelectTrigger className="w-32">
                            <SelectValue />
                          </SelectTrigger>
                          <SelectContent>
                            <SelectItem value="student"></SelectItem>
                            <SelectItem value="teacher"></SelectItem>
                            <SelectItem value="admin"></SelectItem>
                          </SelectContent>
                        </Select>

                        <Button
                          size="sm"
                          variant="outline"
                          onClick={() => {
                            const newPassword = prompt("  :");
                            if (newPassword) {
                              resetUserPassword(username, newPassword);
                            }
                          }}
                        >
                          <Edit className="h-4 w-4" />
                        </Button>

                        <Button
                          size="sm"
                          variant="destructive"
                          onClick={() => deleteUser(username)}
                          disabled={username === 'Starkver'}
                        >
                          <Trash2 className="h-4 w-4" />
                        </Button>
                      </div>
                    </div>
                  );
                })}

                {filteredUsers.length === 0 && (
                  <div className="text-center py-8 text-muted-foreground">
                      
                  </div>
                )}
              </div>
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="create" className="space-y-6">
          <Card>
            <CardHeader>
              <CardTitle className="flex items-center gap-2">
                <UserPlus className="h-5 w-5" />
                  
              </CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div>
                  <Label> :</Label>
                  <Input
                    value={newUser.username}
                    onChange={(e) => setNewUser({...newUser, username: e.target.value})}
                    placeholder="username"
                  />
                </div>
                
                <div>
                  <Label>:</Label>
                  <Input
                    type="password"
                    value={newUser.password}
                    onChange={(e) => setNewUser({...newUser, password: e.target.value})}
                    placeholder=""
                  />
                </div>
                
                <div>
                  <Label>:</Label>
                  <Select
                    value={newUser.role}
                    onValueChange={(role: any) => setNewUser({...newUser, role})}
                  >
                    <SelectTrigger>
                      <SelectValue />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value="student"></SelectItem>
                      <SelectItem value="teacher"></SelectItem>
                      <SelectItem value="admin"></SelectItem>
                    </SelectContent>
                  </Select>
                </div>
                
                <div>
                  <Label> ( ):</Label>
                  <Select
                    value={newUser.course}
                    onValueChange={(course) => setNewUser({...newUser, course})}
                  >
                    <SelectTrigger>
                      <SelectValue placeholder=" " />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value=""> </SelectItem>
                      <SelectItem value="1">1 </SelectItem>
                      <SelectItem value="2">2 </SelectItem>
                      <SelectItem value="3">3 </SelectItem>
                      <SelectItem value="4">4 </SelectItem>
                      <SelectItem value="5">5 </SelectItem>
                      <SelectItem value="6">6 </SelectItem>
                    </SelectContent>
                  </Select>
                </div>
                
                <div className="md:col-span-2">
                  <Label>Email ():</Label>
                  <Input
                    type="email"
                    value={newUser.email}
                    onChange={(e) => setNewUser({...newUser, email: e.target.value})}
                    placeholder="email@example.com"
                  />
                </div>
              </div>

              <Button onClick={createUser} className="w-full">
                <UserPlus className="h-4 w-4 mr-2" />
                 
              </Button>
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="stats" className="space-y-6">
          <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
            <Card>
              <CardHeader>
                <CardTitle className="flex items-center gap-2">
                  <Users className="h-5 w-5" />
                   
                </CardTitle>
              </CardHeader>
              <CardContent>
                <div className="text-3xl font-bold">{Object.keys(users).length}</div>
              </CardContent>
            </Card>

            <Card>
              <CardHeader>
                <CardTitle className="flex items-center gap-2">
                  <User className="h-5 w-5" />
                  
                </CardTitle>
              </CardHeader>
              <CardContent>
                <div className="text-3xl font-bold">
                  {Object.values(users).filter(u => u.role === 'student').length}
                </div>
              </CardContent>
            </Card>

            <Card>
              <CardHeader>
                <CardTitle className="flex items-center gap-2">
                  <GraduationCap className="h-5 w-5" />
                  
                </CardTitle>
              </CardHeader>
              <CardContent>
                <div className="text-3xl font-bold">
                  {Object.values(users).filter(u => u.role === 'teacher').length}
                </div>
              </CardContent>
            </Card>
          </div>

          <Alert>
            <Users className="h-4 w-4" />
            <AlertDescription>
                   .
                 localStorage .
            </AlertDescription>
          </Alert>
        </TabsContent>
      </Tabs>
    </div>
  );
}