import { useState, useEffect } from "react";
import { Card, CardContent, CardHeader, CardTitle } from "./ui/card";
import { Button } from "./ui/button";
import { Textarea } from "./ui/textarea";
import { Input } from "./ui/input";
import { Label } from "./ui/label";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "./ui/tabs";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "./ui/select";
import { Alert, AlertDescription } from "./ui/alert";
import { Separator } from "./ui/separator";
import { 
  Edit, Save, Plus, Trash2, Eye, EyeOff, 
  Home, Building, HelpCircle, Gift, 
  FileText, Monitor, Palette 
} from "lucide-react";
import { toast } from "../lib/toast";

export function AdminContentManager() {
  const [activeSection, setActiveSection] = useState("main");
  const [editingData, setEditingData] = useState("");
  const [newSectionName, setNewSectionName] = useState("");
  const [selectedTemplate, setSelectedTemplate] = useState("");

  //     localStorage
  const getSectionData = (sectionKey: string) => {
    const data = localStorage.getItem(sectionKey);
    return data ? JSON.parse(data) : {};
  };

  //   
  const saveSectionData = (sectionKey: string, data: any) => {
    localStorage.setItem(sectionKey, JSON.stringify(data));
    toast.success(" ");
  };

  //    
  const loadSectionForEdit = (sectionKey: string) => {
    const data = getSectionData(sectionKey);
    setEditingData(JSON.stringify(data, null, 2));
    setActiveSection(sectionKey);
  };

  //  
  const saveChanges = () => {
    try {
      const parsedData = JSON.parse(editingData);
      saveSectionData(activeSection, parsedData);
    } catch (error) {
      toast.error("  JSON ");
    }
  };

  //   
  const createNewSection = () => {
    if (!newSectionName.trim()) {
      toast.error("  ");
      return;
    }
    
    const sectionKey = `admin_section_${newSectionName.toLowerCase().replace(/\s+/g, '_')}`;
    const defaultData = getTemplateData(selectedTemplate, newSectionName);
    
    saveSectionData(sectionKey, defaultData);
    setNewSectionName("");
    setSelectedTemplate("");
    loadSectionForEdit(sectionKey);
  };

  //    
  const getTemplateData = (template: string, title: string) => {
    const templates = {
      page: {
        title,
        content: `# ${title}\n\n   .`,
        type: "page",
        visible: true,
        created: new Date().toISOString()
      },
      info_block: {
        title,
        description: "  ",
        items: [
          { title: " 1", content: "  1" },
          { title: " 2", content: "  2" }
        ],
        type: "info_block",
        visible: true,
        created: new Date().toISOString()
      },
      faq: {
        title,
        questions: [
          { question: " 1?", answer: "   1" },
          { question: " 2?", answer: "   2" }
        ],
        type: "faq",
        visible: true,
        created: new Date().toISOString()
      }
    };
    
    return templates[template as keyof typeof templates] || templates.page;
  };

  //   
  const getAllSections = () => {
    const sections = [
      { key: "main_page", title: " ", icon: Home },
      { key: "campuses", title: "", icon: Building },
      { key: "faq", title: "FAQ", icon: HelpCircle },
      { key: "benefits", title: "", icon: Gift },
      { key: "announcements", title: "", icon: FileText },
      { key: "events", title: "", icon: Monitor }
    ];

    //   
    for (let i = 0; i < localStorage.length; i++) {
      const key = localStorage.key(i);
      if (key && key.startsWith('admin_section_')) {
        const data = getSectionData(key);
        sections.push({
          key,
          title: data.title || key.replace('admin_section_', ''),
          icon: FileText
        });
      }
    }

    return sections;
  };

  //  
  const deleteSection = (sectionKey: string) => {
    if (confirm(" ,     ?")) {
      localStorage.removeItem(sectionKey);
      toast.success(" ");
      if (activeSection === sectionKey) {
        setActiveSection("main_page");
        setEditingData("");
      }
    }
  };

  //  
  const exportSettings = () => {
    const settings: any = {};
    getAllSections().forEach(section => {
      settings[section.key] = getSectionData(section.key);
    });
    
    const blob = new Blob([JSON.stringify(settings, null, 2)], { type: 'application/json' });
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = `site-settings-${new Date().toISOString().split('T')[0]}.json`;
    a.click();
    URL.revokeObjectURL(url);
    toast.success(" ");
  };

  //  
  const importSettings = (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (!file) return;

    const reader = new FileReader();
    reader.onload = (e) => {
      try {
        const settings = JSON.parse(e.target?.result as string);
        Object.entries(settings).forEach(([key, value]) => {
          localStorage.setItem(key, JSON.stringify(value));
        });
        toast.success(" ");
      } catch (error) {
        toast.error("   ");
      }
    };
    reader.readAsText(file);
  };

  const sections = getAllSections();

  return (
    <div className="space-y-6">
      <div className="text-center py-4">
        <h2 className="text-2xl mb-2">  </h2>
        <p className="text-muted-foreground">
              
        </p>
      </div>

      <Tabs defaultValue="editor" className="w-full">
        <TabsList className="grid w-full grid-cols-3">
          <TabsTrigger value="editor"> </TabsTrigger>
          <TabsTrigger value="create"> </TabsTrigger>
          <TabsTrigger value="settings"></TabsTrigger>
        </TabsList>

        <TabsContent value="editor" className="space-y-6">
          <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
            {/*   */}
            <Card>
              <CardHeader>
                <CardTitle className="flex items-center gap-2">
                  <FileText className="h-5 w-5" />
                   
                </CardTitle>
              </CardHeader>
              <CardContent className="space-y-2">
                {sections.map((section) => {
                  const IconComponent = section.icon;
                  return (
                    <div
                      key={section.key}
                      className={`flex items-center justify-between p-3 rounded-lg border cursor-pointer transition-colors ${
                        activeSection === section.key 
                          ? 'border-primary bg-primary/5' 
                          : 'hover:bg-accent'
                      }`}
                      onClick={() => loadSectionForEdit(section.key)}
                    >
                      <div className="flex items-center gap-2">
                        <IconComponent className="h-4 w-4" />
                        <span className="text-sm font-medium">{section.title}</span>
                      </div>
                      {section.key.startsWith('admin_section_') && (
                        <Button
                          size="sm"
                          variant="ghost"
                          onClick={(e) => {
                            e.stopPropagation();
                            deleteSection(section.key);
                          }}
                        >
                          <Trash2 className="h-3 w-3" />
                        </Button>
                      )}
                    </div>
                  );
                })}
              </CardContent>
            </Card>

            {/*  */}
            <div className="lg:col-span-2">
              <Card>
                <CardHeader>
                  <CardTitle className="flex items-center gap-2">
                    <Edit className="h-5 w-5" />
                    : {sections.find(s => s.key === activeSection)?.title || " "}
                  </CardTitle>
                </CardHeader>
                <CardContent className="space-y-4">
                  {activeSection ? (
                    <>
                      <div>
                        <Label>JSON  :</Label>
                        <Textarea
                          value={editingData}
                          onChange={(e) => setEditingData(e.target.value)}
                          rows={20}
                          className="font-mono text-sm"
                          placeholder="   ..."
                        />
                      </div>
                      <div className="flex gap-2">
                        <Button onClick={saveChanges} className="flex-1">
                          <Save className="h-4 w-4 mr-2" />
                           
                        </Button>
                        <Button 
                          variant="outline" 
                          onClick={() => loadSectionForEdit(activeSection)}
                        >
                          
                        </Button>
                      </div>
                    </>
                  ) : (
                    <div className="text-center py-8 text-muted-foreground">
                            
                    </div>
                  )}
                </CardContent>
              </Card>
            </div>
          </div>
        </TabsContent>

        <TabsContent value="create" className="space-y-6">
          <Card>
            <CardHeader>
              <CardTitle className="flex items-center gap-2">
                <Plus className="h-5 w-5" />
                  
              </CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              <div>
                <Label> :</Label>
                <Input
                  value={newSectionName}
                  onChange={(e) => setNewSectionName(e.target.value)}
                  placeholder=":  "
                />
              </div>
              
              <div>
                <Label>:</Label>
                <Select value={selectedTemplate} onValueChange={setSelectedTemplate}>
                  <SelectTrigger>
                    <SelectValue placeholder=" " />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="page"> </SelectItem>
                    <SelectItem value="info_block"> </SelectItem>
                    <SelectItem value="faq">FAQ </SelectItem>
                  </SelectContent>
                </Select>
              </div>

              <Button onClick={createNewSection} className="w-full">
                <Plus className="h-4 w-4 mr-2" />
                 
              </Button>
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="settings" className="space-y-6">
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            <Card>
              <CardHeader>
                <CardTitle className="flex items-center gap-2">
                  <Monitor className="h-5 w-5" />
                  / 
                </CardTitle>
              </CardHeader>
              <CardContent className="space-y-4">
                <div>
                  <Label>   :</Label>
                  <Button onClick={exportSettings} variant="outline" className="w-full mt-2">
                     backup.json
                  </Button>
                </div>
                
                <Separator />
                
                <div>
                  <Label> :</Label>
                  <input
                    type="file"
                    accept=".json"
                    onChange={importSettings}
                    className="mt-2 block w-full text-sm text-foreground
                      file:mr-4 file:py-2 file:px-4
                      file:rounded-lg file:border-0
                      file:text-sm file:font-medium
                      file:bg-primary file:text-primary-foreground
                      hover:file:bg-primary/90"
                  />
                </div>
              </CardContent>
            </Card>

            <Card>
              <CardHeader>
                <CardTitle className="flex items-center gap-2">
                  <Palette className="h-5 w-5" />
                   
                </CardTitle>
              </CardHeader>
              <CardContent className="space-y-4">
                <Button variant="outline" className="w-full">
                  <Eye className="h-4 w-4 mr-2" />
                   
                </Button>
                
                <Button variant="outline" className="w-full">
                  <FileText className="h-4 w-4 mr-2" />
                   
                </Button>

                <Alert>
                  <FileText className="h-4 w-4" />
                  <AlertDescription>
                        localStorage . 
                       .
                  </AlertDescription>
                </Alert>
              </CardContent>
            </Card>
          </div>
        </TabsContent>
      </Tabs>
    </div>
  );
}