//       Excel

interface Student {
  name: string;
  scores: number[];
  totalRating: number;
}

interface Journal {
  id: string;
  course: string;
  group: string;
  faculty: string;
  subject: string;
  maxScore: number;
  students: Student[];
  columns: string[];
  createdAt: string;
}

//    CSV  (  Excel)
function createCSVContent(journal: Journal): string {
  const lines: string[] = [];
  
  //     
  lines.push(`  - ${journal.course} , ${journal.group} `);
  lines.push(`: ${journal.faculty}`);
  lines.push(`: ${journal.subject}`);
  lines.push(` : ${journal.maxScore}`);
  lines.push(` : ${new Date(journal.createdAt).toLocaleDateString('ru-RU')}`);
  lines.push(` : ${new Date().toLocaleDateString('ru-RU')} ${new Date().toLocaleTimeString('ru-RU')}`);
  lines.push(''); //  
  
  //  
  const headers = ['', ' ', ...journal.columns, ' ', '  (%)', ''];
  lines.push(headers.join(';'));
  
  //  
  journal.students.forEach((student, index) => {
    const totalScore = student.scores.reduce((sum, score) => sum + score, 0);
    const grade = getGradeFromRating(student.totalRating);
    
    const row = [
      (index + 1).toString(),
      student.name,
      ...student.scores.map(score => score.toString()),
      totalScore.toString(),
      student.totalRating.toFixed(2),
      grade
    ];
    lines.push(row.join(';'));
  });
  
  // 
  lines.push('');
  lines.push('');
  lines.push(` ;${journal.students.length}`);
  lines.push(` (80%);${journal.students.filter(s => s.totalRating >= 80).length}`);
  lines.push(` (60-79%);${journal.students.filter(s => s.totalRating >= 60 && s.totalRating < 80).length}`);
  lines.push(` (40-59%);${journal.students.filter(s => s.totalRating >= 40 && s.totalRating < 60).length}`);
  lines.push(` (<40%);${journal.students.filter(s => s.totalRating < 40).length}`);
  lines.push(` ;${calculateAverageRating(journal.students).toFixed(2)}%`);
  
  return lines.join('\n');
}

//      
function getGradeFromRating(rating: number): string {
  if (rating >= 80) return '';
  if (rating >= 60) return '';
  if (rating >= 40) return '';
  return '';
}

//     
function calculateAverageRating(students: Student[]): number {
  if (students.length === 0) return 0;
  const totalRating = students.reduce((sum, student) => sum + student.totalRating, 0);
  return totalRating / students.length;
}

//   
export function exportJournalToExcel(journal: Journal): void {
  try {
    //  CSV 
    const csvContent = createCSVContent(journal);
    
    //  BOM      Excel
    const BOM = '\uFEFF';
    const csvWithBOM = BOM + csvContent;
    
    //  Blob
    const blob = new Blob([csvWithBOM], { 
      type: 'text/csv;charset=utf-8' 
    });
    
    //    
    const link = document.createElement('a');
    const url = URL.createObjectURL(blob);
    link.href = url;
    
    //    (   )
    const safeSubject = journal.subject.replace(/[/\\?%*:|"<>]/g, '-');
    const safeGroup = journal.group.replace(/[/\\?%*:|"<>]/g, '-');
    const date = new Date().toLocaleDateString('ru-RU').replace(/\./g, '-');
    const fileName = `_${journal.course}__${safeGroup}__${safeSubject}_${date}.csv`;
    link.download = fileName;
    
    //  
    document.body.appendChild(link);
    link.click();
    
    //  
    setTimeout(() => {
      if (document.body.contains(link)) {
        document.body.removeChild(link);
      }
      URL.revokeObjectURL(url);
    }, 100);
    
  } catch (error) {
    console.error('   :', error);
    throw new Error('   ');
  }
}

//    Excel- HTML 
export function exportJournalToExcelAdvanced(journal: Journal): Promise<void> {
  return new Promise((resolve, reject) => {
    try {
      //  HTML-,  Excel  
      const htmlContent = createExcelCompatibleHTML(journal);
      
      //  Blob  HTML 
      const blob = new Blob([htmlContent], { 
        type: 'application/vnd.ms-excel;charset=utf-8' 
      });
      
      //    
      const link = document.createElement('a');
      const url = URL.createObjectURL(blob);
      link.href = url;
      
      //       .xls
      const safeSubject = journal.subject.replace(/[/\\?%*:|"<>]/g, '-');
      const safeGroup = journal.group.replace(/[/\\?%*:|"<>]/g, '-');
      const date = new Date().toLocaleDateString('ru-RU').replace(/\./g, '-');
      const fileName = `_${journal.course}__${safeGroup}__${safeSubject}_${date}.xls`;
      link.download = fileName;
      
      //  
      document.body.appendChild(link);
      link.click();
      
      //    
      setTimeout(() => {
        if (document.body.contains(link)) {
          document.body.removeChild(link);
        }
        URL.revokeObjectURL(url);
      }, 100);
      
      resolve();
    } catch (error) {
      reject(error);
    }
  });
}

//    HTML-,   Excel
function createExcelCompatibleHTML(journal: Journal): string {
  const html = `
<!DOCTYPE html>
<html xmlns:o="urn:schemas-microsoft-com:office:office" xmlns:x="urn:schemas-microsoft-com:office:excel">
<head>
  <meta charset="UTF-8">
  <meta name="ProgId" content="Excel.Sheet">
  <meta name="Generator" content="Educational Portal">
  <style>
    body { font-family: Arial, sans-serif; }
    table { border-collapse: collapse; width: 100%; }
    th, td { border: 1px solid #000; padding: 5px; text-align: left; }
    th { background-color: #f0f0f0; font-weight: bold; }
    .header-info { margin-bottom: 20px; }
    .stats-table { margin-top: 30px; }
    .number-cell { text-align: center; }
    .rating-cell { text-align: center; font-weight: bold; }
    .excellent { color: #059669; }
    .good { color: #d97706; }
    .satisfactory { color: #0891b2; }
    .unsatisfactory { color: #dc2626; }
  </style>
</head>
<body>
  <div class="header-info">
    <h1>  - ${journal.course} , ${journal.group} </h1>
    <p><strong>:</strong> ${journal.faculty}</p>
    <p><strong>:</strong> ${journal.subject}</p>
    <p><strong> :</strong> ${journal.maxScore}</p>
    <p><strong> :</strong> ${new Date(journal.createdAt).toLocaleDateString('ru-RU')}</p>
    <p><strong> :</strong> ${new Date().toLocaleDateString('ru-RU')} ${new Date().toLocaleTimeString('ru-RU')}</p>
  </div>

  <table>
    <thead>
      <tr>
        <th class="number-cell"></th>
        <th> </th>
        ${journal.columns.map(col => `<th class="number-cell">${col}</th>`).join('')}
        <th class="number-cell"> </th>
        <th class="number-cell">  (%)</th>
        <th></th>
      </tr>
    </thead>
    <tbody>
      ${journal.students.map((student, index) => {
        const totalScore = student.scores.reduce((sum, score) => sum + score, 0);
        const grade = getGradeFromRating(student.totalRating);
        const gradeClass = getGradeClass(student.totalRating);
        
        return `
        <tr>
          <td class="number-cell">${index + 1}</td>
          <td>${student.name}</td>
          ${student.scores.map(score => `<td class="number-cell">${score}</td>`).join('')}
          <td class="number-cell">${totalScore}</td>
          <td class="rating-cell ${gradeClass}">${student.totalRating.toFixed(2)}</td>
          <td class="${gradeClass}">${grade}</td>
        </tr>`;
      }).join('')}
    </tbody>
  </table>

  <div class="stats-table">
    <h2></h2>
    <table style="width: 400px;">
      <tr><th></th><th></th></tr>
      <tr><td> </td><td class="number-cell">${journal.students.length}</td></tr>
      <tr><td> (80%)</td><td class="number-cell excellent">${journal.students.filter(s => s.totalRating >= 80).length}</td></tr>
      <tr><td> (60-79%)</td><td class="number-cell good">${journal.students.filter(s => s.totalRating >= 60 && s.totalRating < 80).length}</td></tr>
      <tr><td> (40-59%)</td><td class="number-cell satisfactory">${journal.students.filter(s => s.totalRating >= 40 && s.totalRating < 60).length}</td></tr>
      <tr><td> (&lt;40%)</td><td class="number-cell unsatisfactory">${journal.students.filter(s => s.totalRating < 40).length}</td></tr>
      <tr><td> </td><td class="number-cell">${calculateAverageRating(journal.students).toFixed(2)}%</td></tr>
    </table>
  </div>
</body>
</html>`;
  
  return html;
}

//    CSS   
function getGradeClass(rating: number): string {
  if (rating >= 80) return 'excellent';
  if (rating >= 60) return 'good';
  if (rating >= 40) return 'satisfactory';
  return 'unsatisfactory';
}