import { useEffect } from "react";
import { useAuth } from "./AuthContext";

interface Notification {
  id: string;
  title: string;
  message: string;
  type: 'info' | 'warning' | 'success' | 'error';
  targetUsers: 'all' | 'students' | 'teachers' | 'specific';
  specificUsers?: string[];
  expiryDate?: string;
  createdAt: string;
  createdBy: string;
  isActive: boolean;
}

export function NotificationDemo() {
  const { user } = useAuth();

  useEffect(() => {
    //        
    if (!user?.role) return;
    
    try {
      const existing = localStorage.getItem('adminNotifications');
      if (!existing && user.role === 'admin') {
        const demoNotifications: Notification[] = [
          {
            id: 'demo-1',
            title: '    !',
            message: '          .       .',
            type: 'info',
            targetUsers: 'all',
            createdAt: new Date().toISOString(),
            createdBy: 'admin',
            isActive: true
          },
          {
            id: 'demo-2',
            title: '  ',
            message: '           .',
            type: 'success',
            targetUsers: 'all',
            createdAt: new Date(Date.now() - 1000 * 60 * 5).toISOString(), // 5  
            createdBy: 'admin',
            isActive: true
          },
          {
            id: 'demo-3',
            title: '   ',
            message: '     .  .',
            type: 'warning',
            targetUsers: 'students',
            createdAt: new Date(Date.now() - 1000 * 60 * 30).toISOString(), // 30  
            createdBy: 'admin',
            isActive: true
          }
        ];

        localStorage.setItem('adminNotifications', JSON.stringify(demoNotifications));
        console.log('  ');
      }
    } catch (error) {
      console.error('Error creating demo notifications:', error);
    }
  }, [user?.role]);

  return null; //     
}