import { useState } from "react";
import { useAuth } from "./AuthContext";
import { Card, CardContent, CardHeader, CardTitle } from "./ui/card";
import { Button } from "./ui/button";
import { Input } from "./ui/input";
import { Label } from "./ui/label";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "./ui/select";
import { Alert, AlertDescription } from "./ui/alert";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "./ui/tabs";
import { AdminFileManager } from "./AdminFileManager";
import { AchievementsManager } from "./AchievementsManager";
import { StudentAchievements } from "./StudentAchievements";
import { AdminContentManager } from "./AdminContentManager";
import { AdminUserManager } from "./AdminUserManager";
import { AdminSystemSettings } from "./AdminSystemSettings";
import { AdminNotificationCenter } from "./AdminNotificationCenter";
import { User, Lock, BookOpen, Save, CheckCircle, Files, Trophy, Edit, Users, Settings, Bell } from "lucide-react";

export function ProfileSection() {
  const { user, logout } = useAuth();
  const [currentPassword, setCurrentPassword] = useState("");
  const [newPassword, setNewPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [selectedCourse, setSelectedCourse] = useState(user?.course || "");
  const [isChangingPassword, setIsChangingPassword] = useState(false);

  const handlePasswordChange = () => {
    if (!currentPassword || !newPassword || !confirmPassword) {
      alert("    ");
      return;
    }

    if (newPassword !== confirmPassword) {
      alert("   ");
      return;
    }

    if (newPassword.length < 6) {
      alert("     6 ");
      return;
    }

    //   
    const users = JSON.parse(localStorage.getItem('users') || '{}');
    const currentUserKey = `${user?.username}_${currentPassword}`;
    
    if (!users[currentUserKey]) {
      alert("  ");
      return;
    }

    //         
    const userData = users[currentUserKey];
    delete users[currentUserKey];
    
    const newUserKey = `${user?.username}_${newPassword}`;
    users[newUserKey] = { ...userData, course: selectedCourse };
    
    localStorage.setItem('users', JSON.stringify(users));
    
    //   
    const updatedUser = { ...userData, course: selectedCourse };
    localStorage.setItem('currentUser', JSON.stringify(updatedUser));
    
    alert("  !    ");
    
    //      
    setTimeout(() => {
      logout();
    }, 1500);
  };

  const handleCourseChange = () => {
    if (!selectedCourse) {
      alert(" ");
      return;
    }

    //    (     )
    if (!currentPassword) {
      alert("     ");
      return;
    }

    const users = JSON.parse(localStorage.getItem('users') || '{}');
    const currentUserKey = `${user?.username}_${currentPassword}`;
    
    if (!users[currentUserKey]) {
      alert(" ");
      return;
    }

    //  
    users[currentUserKey] = { ...users[currentUserKey], course: selectedCourse };
    localStorage.setItem('users', JSON.stringify(users));
    
    //   
    const updatedUser = { ...users[currentUserKey] };
    localStorage.setItem('currentUser', JSON.stringify(updatedUser));
    
    alert("  !");
    
    //     
    setTimeout(() => {
      window.location.reload();
    }, 1000);
  };

  if (!user) {
    return <div>  </div>;
  }

  return (
    <div className="space-y-6">
      <div className="text-center py-8">
        <h1 className="text-3xl md:text-4xl mb-4"> </h1>
        <p className="text-lg text-muted-foreground max-w-2xl mx-auto">
          {user?.role === 'admin' 
            ? '      ' 
            : '   '
          }
        </p>
      </div>

      {user?.role === 'admin' ? (
        <Tabs defaultValue="profile" className="w-full">
          <TabsList className="grid w-full grid-cols-7">
            <TabsTrigger value="profile"></TabsTrigger>
            <TabsTrigger value="files"></TabsTrigger>
            <TabsTrigger value="achievements"></TabsTrigger>
            <TabsTrigger value="content"></TabsTrigger>
            <TabsTrigger value="users"></TabsTrigger>
            <TabsTrigger value="notifications"></TabsTrigger>
            <TabsTrigger value="system"></TabsTrigger>
          </TabsList>
          
          <TabsContent value="profile" className="space-y-6">
            <div className="grid gap-6 max-w-2xl mx-auto">
              {/*    */}
              <Card>
                <CardHeader>
                  <CardTitle className="flex items-center gap-2">
                    <User className="h-5 w-5" />
                      
                  </CardTitle>
                </CardHeader>
                <CardContent className="space-y-4">
                  <div className="grid grid-cols-2 gap-4">
                    <div>
                      <Label> </Label>
                      <Input value={user.username} disabled />
                    </div>
                    <div>
                      <Label></Label>
                      <Input 
                        value={user.role === 'admin' ? '' : user.role === 'teacher' ? '' : ''} 
                        disabled 
                      />
                    </div>
                  </div>
                  {user.course && (
                    <div>
                      <Label> </Label>
                      <Input value={user.course} disabled />
                    </div>
                  )}
                </CardContent>
              </Card>

              {/*   -   - */}
              {user.role !== 'admin' && (
                <Card>
                  <CardHeader>
                    <CardTitle className="flex items-center gap-2">
                      <BookOpen className="h-5 w-5" />
                       
                    </CardTitle>
                  </CardHeader>
                  <CardContent className="space-y-4">
                    <div>
                      <Label> </Label>
                      <Select value={selectedCourse} onValueChange={setSelectedCourse}>
                        <SelectTrigger>
                          <SelectValue placeholder=" " />
                        </SelectTrigger>
                        <SelectContent>
                          <SelectItem value="1">1 </SelectItem>
                          <SelectItem value="2">2 </SelectItem>
                          <SelectItem value="3">3 </SelectItem>
                          <SelectItem value="4">4 </SelectItem>
                          <SelectItem value="5">5 </SelectItem>
                          <SelectItem value="6">6 </SelectItem>
                        </SelectContent>
                      </Select>
                    </div>
                    <div>
                      <Label> </Label>
                      <Input
                        type="password"
                        value={currentPassword}
                        onChange={(e) => setCurrentPassword(e.target.value)}
                        placeholder="  "
                      />
                    </div>
                    <Button onClick={handleCourseChange} className="w-full">
                      <Save className="h-4 w-4 mr-2" />
                       
                    </Button>
                  </CardContent>
                </Card>
              )}

              {/*   */}
              <Card>
                <CardHeader>
                  <CardTitle className="flex items-center gap-2">
                    <Lock className="h-5 w-5" />
                     
                  </CardTitle>
                </CardHeader>
                <CardContent className="space-y-4">
                  {!isChangingPassword ? (
                    <Button 
                      onClick={() => setIsChangingPassword(true)}
                      variant="outline" 
                      className="w-full"
                    >
                       
                    </Button>
                  ) : (
                    <>
                      <div>
                        <Label> </Label>
                        <Input
                          type="password"
                          value={currentPassword}
                          onChange={(e) => setCurrentPassword(e.target.value)}
                          placeholder="  "
                        />
                      </div>
                      <div>
                        <Label> </Label>
                        <Input
                          type="password"
                          value={newPassword}
                          onChange={(e) => setNewPassword(e.target.value)}
                          placeholder="  "
                        />
                      </div>
                      <div>
                        <Label>  </Label>
                        <Input
                          type="password"
                          value={confirmPassword}
                          onChange={(e) => setConfirmPassword(e.target.value)}
                          placeholder="  "
                        />
                      </div>
                      <Alert>
                        <CheckCircle className="h-4 w-4" />
                        <AlertDescription>
                                  . 
                              .
                        </AlertDescription>
                      </Alert>
                      <div className="flex gap-2">
                        <Button onClick={handlePasswordChange} className="flex-1">
                          <Save className="h-4 w-4 mr-2" />
                           
                        </Button>
                        <Button 
                          variant="outline" 
                          onClick={() => {
                            setIsChangingPassword(false);
                            setCurrentPassword("");
                            setNewPassword("");
                            setConfirmPassword("");
                          }}
                        >
                          
                        </Button>
                      </div>
                    </>
                  )}
                </CardContent>
              </Card>
            </div>
          </TabsContent>

          <TabsContent value="files" className="space-y-6">
            <AdminFileManager />
          </TabsContent>

          <TabsContent value="achievements" className="space-y-6">
            <AchievementsManager />
          </TabsContent>

          <TabsContent value="content" className="space-y-6">
            <AdminContentManager />
          </TabsContent>

          <TabsContent value="users" className="space-y-6">
            <AdminUserManager />
          </TabsContent>

          <TabsContent value="notifications" className="space-y-6">
            <AdminNotificationCenter />
          </TabsContent>

          <TabsContent value="system" className="space-y-6">
            <AdminSystemSettings />
          </TabsContent>
        </Tabs>
      ) : user?.role === 'student' ? (
        <Tabs defaultValue="profile" className="w-full">
          <TabsList className="grid w-full grid-cols-2">
            <TabsTrigger value="profile"> </TabsTrigger>
            <TabsTrigger value="achievements"> </TabsTrigger>
          </TabsList>
          
          <TabsContent value="profile" className="space-y-6">
            <div className="grid gap-6 max-w-2xl mx-auto">
              {/*    */}
              <Card>
            <CardHeader>
              <CardTitle className="flex items-center gap-2">
                <User className="h-5 w-5" />
                  
              </CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              <div className="grid grid-cols-2 gap-4">
                <div>
                  <Label> </Label>
                  <Input value={user?.username || ''} disabled />
                </div>
                <div>
                  <Label></Label>
                  <Input 
                    value={user?.role === 'admin' ? '' : user?.role === 'teacher' ? '' : ''} 
                    disabled 
                  />
                </div>
              </div>
              {user?.course && (
                <div>
                  <Label> </Label>
                  <Input value={user.course} disabled />
                </div>
              )}
            </CardContent>
          </Card>

          {/*   -   - */}
          <Card>
            <CardHeader>
              <CardTitle className="flex items-center gap-2">
                <BookOpen className="h-5 w-5" />
                 
              </CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              <div>
                <Label> </Label>
                <Select value={selectedCourse} onValueChange={setSelectedCourse}>
                  <SelectTrigger>
                    <SelectValue placeholder=" " />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="1">1 </SelectItem>
                    <SelectItem value="2">2 </SelectItem>
                    <SelectItem value="3">3 </SelectItem>
                    <SelectItem value="4">4 </SelectItem>
                    <SelectItem value="5">5 </SelectItem>
                    <SelectItem value="6">6 </SelectItem>
                  </SelectContent>
                </Select>
              </div>
              <div>
                <Label> </Label>
                <Input
                  type="password"
                  value={currentPassword}
                  onChange={(e) => setCurrentPassword(e.target.value)}
                  placeholder="  "
                />
              </div>
              <Button onClick={handleCourseChange} className="w-full">
                <Save className="h-4 w-4 mr-2" />
                 
              </Button>
            </CardContent>
          </Card>

          {/*   */}
          <Card>
            <CardHeader>
              <CardTitle className="flex items-center gap-2">
                <Lock className="h-5 w-5" />
                 
              </CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              {!isChangingPassword ? (
                <Button 
                  onClick={() => setIsChangingPassword(true)}
                  variant="outline" 
                  className="w-full"
                >
                   
                </Button>
              ) : (
                <>
                  <div>
                    <Label> </Label>
                    <Input
                      type="password"
                      value={currentPassword}
                      onChange={(e) => setCurrentPassword(e.target.value)}
                      placeholder="  "
                    />
                  </div>
                  <div>
                    <Label> </Label>
                    <Input
                      type="password"
                      value={newPassword}
                      onChange={(e) => setNewPassword(e.target.value)}
                      placeholder="  "
                    />
                  </div>
                  <div>
                    <Label>  </Label>
                    <Input
                      type="password"
                      value={confirmPassword}
                      onChange={(e) => setConfirmPassword(e.target.value)}
                      placeholder="  "
                    />
                  </div>
                  <Alert>
                    <CheckCircle className="h-4 w-4" />
                    <AlertDescription>
                              . 
                          .
                    </AlertDescription>
                  </Alert>
                  <div className="flex gap-2">
                    <Button onClick={handlePasswordChange} className="flex-1">
                      <Save className="h-4 w-4 mr-2" />
                       
                    </Button>
                    <Button 
                      variant="outline" 
                      onClick={() => {
                        setIsChangingPassword(false);
                        setCurrentPassword("");
                        setNewPassword("");
                        setConfirmPassword("");
                      }}
                    >
                      
                    </Button>
                  </div>
                </>
              )}
            </CardContent>
          </Card>
            </div>
          </TabsContent>

          <TabsContent value="achievements" className="space-y-6">
            {user?.username && user?.course && (
              <StudentAchievements 
                studentId={user.username} 
                studentCourse={user.course}
              />
            )}
          </TabsContent>
        </Tabs>
      ) : (
        /*   */
        <div className="grid gap-6 max-w-2xl mx-auto">
          {/*    */}
          <Card>
            <CardHeader>
              <CardTitle className="flex items-center gap-2">
                <User className="h-5 w-5" />
                  
              </CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              <div className="grid grid-cols-2 gap-4">
                <div>
                  <Label> </Label>
                  <Input value={user?.username || ''} disabled />
                </div>
                <div>
                  <Label></Label>
                  <Input 
                    value={user?.role === 'admin' ? '' : user?.role === 'teacher' ? '' : ''} 
                    disabled 
                  />
                </div>
              </div>
              {user?.course && (
                <div>
                  <Label> </Label>
                  <Input value={user.course} disabled />
                </div>
              )}
            </CardContent>
          </Card>

          {/*   */}
          <Card>
            <CardHeader>
              <CardTitle className="flex items-center gap-2">
                <Lock className="h-5 w-5" />
                 
              </CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              {!isChangingPassword ? (
                <Button 
                  onClick={() => setIsChangingPassword(true)}
                  variant="outline" 
                  className="w-full"
                >
                   
                </Button>
              ) : (
                <>
                  <div>
                    <Label> </Label>
                    <Input
                      type="password"
                      value={currentPassword}
                      onChange={(e) => setCurrentPassword(e.target.value)}
                      placeholder="  "
                    />
                  </div>
                  <div>
                    <Label> </Label>
                    <Input
                      type="password"
                      value={newPassword}
                      onChange={(e) => setNewPassword(e.target.value)}
                      placeholder="  "
                    />
                  </div>
                  <div>
                    <Label>  </Label>
                    <Input
                      type="password"
                      value={confirmPassword}
                      onChange={(e) => setConfirmPassword(e.target.value)}
                      placeholder="  "
                    />
                  </div>
                  <Alert>
                    <CheckCircle className="h-4 w-4" />
                    <AlertDescription>
                              . 
                          .
                    </AlertDescription>
                  </Alert>
                  <div className="flex gap-2">
                    <Button onClick={handlePasswordChange} className="flex-1">
                      <Save className="h-4 w-4 mr-2" />
                       
                    </Button>
                    <Button 
                      variant="outline" 
                      onClick={() => {
                        setIsChangingPassword(false);
                        setCurrentPassword("");
                        setNewPassword("");
                        setConfirmPassword("");
                      }}
                    >
                      
                    </Button>
                  </div>
                </>
              )}
            </CardContent>
          </Card>
        </div>
      )}
    </div>
  );
}