import { useState } from "react";
import { Button } from "./ui/button";
import { Input } from "./ui/input";
import { Label } from "./ui/label";
import { Textarea } from "./ui/textarea";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "./ui/select";
import { Card, CardContent, CardHeader, CardTitle } from "./ui/card";
import { Bell, Send, X } from "lucide-react";
import { toast } from "../lib/toast";

interface QuickNotificationCreatorProps {
  onClose: () => void;
  onSuccess: () => void;
}

interface Notification {
  id: string;
  title: string;
  message: string;
  type: 'info' | 'warning' | 'success' | 'error';
  targetUsers: 'all' | 'students' | 'teachers' | 'specific';
  specificUsers?: string[];
  expiryDate?: string;
  createdAt: string;
  createdBy: string;
  isActive: boolean;
}

export function QuickNotificationCreator({ onClose, onSuccess }: QuickNotificationCreatorProps) {
  const [formData, setFormData] = useState({
    title: '',
    message: '',
    type: 'info' as const,
    targetUsers: 'all' as const,
    specificUsers: '',
    expiryDate: ''
  });

  const handleSubmit = () => {
    if (!formData.title.trim() || !formData.message.trim()) {
      toast.error('   ');
      return;
    }

    const newNotification: Notification = {
      id: Date.now().toString(),
      title: formData.title.trim(),
      message: formData.message.trim(),
      type: formData.type,
      targetUsers: formData.targetUsers,
      specificUsers: formData.specificUsers ? formData.specificUsers.split(',').map(u => u.trim()) : undefined,
      expiryDate: formData.expiryDate || undefined,
      createdAt: new Date().toISOString(),
      createdBy: 'admin',
      isActive: true
    };

    //   localStorage
    const existing = JSON.parse(localStorage.getItem('adminNotifications') || '[]');
    const updated = [newNotification, ...existing];
    localStorage.setItem('adminNotifications', JSON.stringify(updated));

    toast.success('  ');
    onSuccess();
    onClose();
  };

  const getTypeDescription = (type: string) => {
    switch (type) {
      case 'info': return ' ';
      case 'warning': return '   ';
      case 'success': return '   ';
      case 'error': return '   ';
      default: return '';
    }
  };

  const getAudienceDescription = (audience: string) => {
    switch (audience) {
      case 'all': return '  ';
      case 'students': return ' ';
      case 'teachers': return ' ';
      case 'specific': return ' ';
      default: return '';
    }
  };

  return (
    <div className="fixed inset-0 bg-black/50 z-[100] flex items-center justify-center p-4">
      <Card className="w-full max-w-md max-h-[90vh] overflow-y-auto">
        <CardHeader>
          <div className="flex items-center justify-between">
            <CardTitle className="flex items-center gap-2">
              <Bell className="h-5 w-5" />
               
            </CardTitle>
            <Button variant="ghost" size="sm" onClick={onClose}>
              <X className="h-4 w-4" />
            </Button>
          </div>
        </CardHeader>
        <CardContent className="space-y-4">
          <div className="space-y-2">
            <Label htmlFor="quick-title"> *</Label>
            <Input
              id="quick-title"
              value={formData.title}
              onChange={(e) => setFormData(prev => ({ ...prev, title: e.target.value }))}
              placeholder="  "
              maxLength={100}
            />
          </div>

          <div className="space-y-2">
            <Label htmlFor="quick-message"> *</Label>
            <Textarea
              id="quick-message"
              value={formData.message}
              onChange={(e) => setFormData(prev => ({ ...prev, message: e.target.value }))}
              placeholder=" ..."
              rows={3}
              maxLength={500}
            />
            <div className="text-xs text-muted-foreground text-right">
              {formData.message.length}/500
            </div>
          </div>

          <div className="space-y-2">
            <Label htmlFor="quick-type"> </Label>
            <Select 
              value={formData.type} 
              onValueChange={(value: any) => setFormData(prev => ({ ...prev, type: value }))}
            >
              <SelectTrigger>
                <SelectValue />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="info"> </SelectItem>
                <SelectItem value="warning"> </SelectItem>
                <SelectItem value="success"> </SelectItem>
                <SelectItem value="error"> </SelectItem>
              </SelectContent>
            </Select>
            <div className="text-xs text-muted-foreground">
              {getTypeDescription(formData.type)}
            </div>
          </div>

          <div className="space-y-2">
            <Label htmlFor="quick-audience"></Label>
            <Select 
              value={formData.targetUsers} 
              onValueChange={(value: any) => setFormData(prev => ({ ...prev, targetUsers: value }))}
            >
              <SelectTrigger>
                <SelectValue />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="all">  </SelectItem>
                <SelectItem value="students">  </SelectItem>
                <SelectItem value="teachers">  </SelectItem>
                <SelectItem value="specific">  </SelectItem>
              </SelectContent>
            </Select>
            <div className="text-xs text-muted-foreground">
              {getAudienceDescription(formData.targetUsers)}
            </div>
          </div>

          {formData.targetUsers === 'specific' && (
            <div className="space-y-2">
              <Label htmlFor="quick-users"> </Label>
              <Input
                id="quick-users"
                value={formData.specificUsers}
                onChange={(e) => setFormData(prev => ({ ...prev, specificUsers: e.target.value }))}
                placeholder="user1, user2, user3"
              />
              <div className="text-xs text-muted-foreground">
                    
              </div>
            </div>
          )}

          <div className="space-y-2">
            <Label htmlFor="quick-expiry">  ()</Label>
            <Input
              id="quick-expiry"
              type="datetime-local"
              value={formData.expiryDate}
              onChange={(e) => setFormData(prev => ({ ...prev, expiryDate: e.target.value }))}
            />
            <div className="text-xs text-muted-foreground">
                  
            </div>
          </div>

          <div className="flex gap-2 pt-4">
            <Button onClick={handleSubmit} className="flex-1">
              <Send className="h-4 w-4 mr-2" />
              
            </Button>
            <Button variant="outline" onClick={onClose}>
              
            </Button>
          </div>
        </CardContent>
      </Card>
    </div>
  );
}